.cux-radio-btn {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  color: #808499;
  font-size: 11px;
  margin: 0 10px;
  cursor: pointer;
  outline: none;
}

.cux-radio-btn:not([checked]) {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  opacity: 1;
}

.cux-radio-btn:not([checked]):disabled {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  opacity: 0.5;
  cursor: default;
}

.cux-radio-btn:not([disabled]):checked {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  opacity: 1;
}

.cux-radio-btn:checked:disabled {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  opacity: 0.5;
  cursor: default;
}
